import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col } from 'antd';
import routeConstant from 'routes/route/routeConstant';
import { InfoCard } from 'components/common';
import {
  getQuickLinkBonusGetPrizeClassificationNo,
  getQuickLinkBonusGetPrizeNoteNo,
} from 'services/quicklink';

const COLORS = [
  '#0088FE',
  '#00C49F',
  '#FFBB28',
  '#FF8042',
  '#0088fed9',
  '#00c49fd9',
  '#ffbb28d9',
  '#ff8042d9',
];

const data = [
  {
    title: 'Xếp loại học bổng',
    service: getQuickLinkBonusGetPrizeClassificationNo,
    url: routeConstant.HOC_BONG__XEP_LOAI,
  },
  {
    title: 'QĐ học bổng',
    service: getQuickLinkBonusGetPrizeNoteNo,
    url: routeConstant.HOC_BONG__NHAP_QUYET_DINH,
  },
];
class DashboardBonu extends Component {
  state = {};
  render() {
    return (
      <Row gutter={24}>
        {data.map((item, index) => (
          <Col span={6} key={item.title}>
            <InfoCard
              backgroundColor={COLORS[index % 8]}
              title={item.title}
              service={item.service}
              icon={item.icon}
              url={item.url}
            />
          </Col>
        ))}
      </Row>
    );
  }
}

export default props => (
  <Page {...props}>
    <DashboardBonu {...props} />
  </Page>
);



